<template>
  <div class="login">
    <van-nav-bar
      title="登录"
      left-text="注册"
      right-text="跳过"
      @click-left="goPage('Register')"
      @click-right="goPage('TakeOutFood')"
    />
    <div class="logo">
      <img class="auto-img" src="../assets/images/xin.jpg" alt="">
    </div>

    <van-form @submit="login">
      <van-field
        v-model="userInfo.phone"
        type="text"
        label="手机号"
        placeholder="输入11位手机号"
        left-icon="phone-o"
        maxlength="11"
      />

      <van-field
        v-model="userInfo.password"
        :type="isEye ? 'text' : 'password'"
        label="密码"
        placeholder="字母数字_组合(6-16位)"
        left-icon="lock"
        :right-icon="isEye ? 'eye-o' : 'closed-eye'"
        @click-right-icon="viewPassword"
        maxlength="16"
      />
      <div class="login-btn">
        <van-button round block type="info" color="#B28850" native-type="submit" @click="addpush('TakeOutFood')">立即登录</van-button>

        <div class="forgot">
          <span class="fr">忘记密码？</span>
        </div>
      </div>
      
    </van-form>
  </div>
</template>

<script>

  import '../assets/less/Login.less'

  import validForm from '../assets/js/validForm'

  export default {
    data() {
      return {

        userInfo: {
          phone: '',
          password: ''
        },

        isEye: false

      }
    },

    methods: {

      addpush(name){
        this.$router.push({name})
      },
      goPage(name) {
        this.$router.push({name});
      },

      viewPassword() {
        this.isEye = !this.isEye;
      },

      login() {

        if (validForm.validUserForm(this.userInfo)) {

          let data = Object.assign({}, this.userInfo);

          data.appkey = this.appkey;

          this.$toast.loading({
            message: '加载中...',
            forbidClick: true,

            duration:0
          });

          this.axios({
            method: 'POST',
            url: '/login',

            data
          }).then(result => {
            this.$toast.clear();
            if (result.data.code == 200) {

              localStorage.setItem('_Tok', result.data.token);
              
            } else {
              this.$toast(result.data.msg);
            }
          }).catch(err => {
            this.$toast.clear();
          })
        }

      }
    }
  }
</script>

<style lang="less" scoped>
  .forgot{
    margin-top: 20px;
  }
</style>